body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@-webkit-keyframes fadeIn {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

.array-container {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    z-index: -1;
}

.array-bar {
    width: 100px;
    background-color: rgb(90, 220, 190);
    display: inline-block;
    margin: 0 1px;
    /*transition: height 0.05s ease-in-out;*/
    border-radius: 2px;
}

button {
    border: 0px solid;
    padding: 19px;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    border-radius: 0px;
    margin-top: auto;
    margin-left: auto;
    background-color: #FFF;
    float: left;
    width: 8.3333333333%;
}


button:hover {
    background-color: #c0d6cf;
}

body {
    -webkit-animation: fadeIn .5s;
            animation: fadeIn .5s;

    background-color: #2c504c;
}

h3 {
    font-size: .8em;
    font-weight: 500;
    text-align: center;
    color: rgba(0, 0, 0, 0.37);
    border-radius: 100px;
    padding: .5em;
    left: 0;
    right: 11px;
    position: absolute;
    margin: auto;
    margin-top: 68.5px;
}

br {
    display: block;
    content: "";
    margin-top: 2em;
    margin-bottom: 1em;
    margin-left: auto;
    margin-right: auto;
    border-width: 1px;
}

input {
    -webkit-appearance: none;
    position: absolute;
    height: .5em;
    width: 50%;
    background-color: rgba(90, 220, 190, 0.4);
    border-radius: 20px;
    top: 100px;
    position: fixed;
    left: 0;
    right: 0;
    margin: auto;
}

input::-webkit-slider-thumb {
    -webkit-appearance: none;
    height: 1em;
    width: 1em;
    border-radius: 50%;
    background-color: rgb(90, 220, 190);
    cursor: pointer;
    position: relative;
}

.alg-info-analysis {
    position: absolute;
    left: 9px;
    margin: auto;
    margin-top: 50px;
    font-size: 1em;
    font-weight: 500;
    font-style: italic;
    text-align: left;
    color: rgba(90, 220, 190, 0.4);
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
